*{
  padding: 0;
  margin: 0;
  font-family: 'Arial';
}

body{
  padding: 10px;
  background:#025951;
}
nav{
  margin-top: 20px;
  margin: auto;
  width: 90%;
  text-align: center;
  padding: 10px;
  display: flex;
  flex-flow: row;
  border: 2px solid black;
  border-radius: 5px;
  box-shadow: 1px 1px 10px;
}

img{
align-items: center;

}

h1{
  margin-top: 5px;
  align-items: center;
}

main{
  margin: 20px;
  text-align: center;
}

input{
  border: 3px solid;
  padding: 10px;
  border-radius: 5px;font-size: 20px;
  box-shadow: 1px 1px 10px;
  margin-bottom: 30px;
}

button{
  padding: 5px;
  margin: 10px;
  background-color: #038C3E;
  border: 2px solid;
  border-radius: 5px;
  font-weight: bold;
  font-size: 20px;
}

#show{
  margin: 20px;
  padding: 10px;
}

p{
  font-size: 25px;
  color: red;
  text-shadow: 10px 10px 10px black;
  font-weight: bold;
}